import React, { useEffect } from "react";
import { observer, inject } from "mobx-react";
import { toJS } from "mobx";
import { NavBar, Icon, Button, List, Checkbox, Flex } from "antd-mobile";
import storage from "@/utils/storage";
import SelectUserList from "./list";

const CheckboxItem = Checkbox.CheckboxItem;
type IProps = {
  store: any;
  location: any;
  history: any;
  title: string;
};
type IState = {
  list: Array<any>;
};

const RemovePerson = observer((props: IProps) => {
  useEffect(() => {
    const { state } = props.location;
    if (state) {
      const { initStore } = props.store;
      initStore(state);
    }
  }, []);
  const onLeftClick = () => {
    props.history.goBack();
  };

  const onCheckBox = (e: any) => {
    const { allCheckBoxHandle, clearCheckBoxHandle } = props.store;
    if (e.target.checked) {
      allCheckBoxHandle();
    } else {
      clearCheckBoxHandle();
    }
  };
  const removePersonHandle = () => {
    const { users, pathname } = props.store;
    const state = {
      pathname: "/remove_person",
      selected: toJS(users, {
        recurseEverything: true
      })
    };
    storage.setItem("selectedPerson", JSON.stringify(state));
    props.history.goBack();
    // props.history.push({
    //   pathname,
    //   state
    // });
  };
  const { pageTitle } = props.store;
  return (
    <div>
      <NavBar
        mode="dark"
        icon={<Icon type="left" />}
        onLeftClick={onLeftClick}
        rightContent={
          <Button type="primary" size="small" onClick={removePersonHandle}>
            移除
          </Button>
        }
      >
        {pageTitle}
      </NavBar>
      <List
        style={{
          height: document.documentElement.clientHeight - 92,
          overflow: "auto"
        }}
      >
        <CheckboxItem
          onChange={(e: any) => {
            onCheckBox(e);
          }}
        >
          全选
        </CheckboxItem>
        <SelectUserList model={props.store} />
      </List>
    </div>
  );
});

export default inject((stores: any) => ({
  store: stores.removePersonStore
}))(RemovePerson);
