import React from "react";
import SelectUserItem from "@/pages/choosePerson/SelectUserList/item";

import { observer } from "mobx-react";
import { IUser } from "../type";

interface IDepartmentListProps {
  model: any;
}

@observer
export default class DepartmentList extends React.Component<
  IDepartmentListProps
> {
  render() {
    const { dataSource, usersMap, onSelectChange } = this.props.model;

    return (
      <>
        {dataSource.map((item: IUser, index: number) => (
          <SelectUserItem
            key={item.memberId}
            rowData={item}
            index={index}
            checked={usersMap.has(item.memberId)}
            onChange={onSelectChange}
          />
        ))}
      </>
    );
  }
}
